<template>
  <div class="instagram-container">
    <div class="instagram-post"
         v-for="(post, index) in posts"
         :key="post.id">
      <div class="header level">
        <div class="level-left">
          <img class="image" :src="post.userImage"/>
          <div class="header-text">
            <span class="username">{{ post.username }}</span>
            <span class="location">{{ post.location }}</span>
          </div>
        </div>
      </div>
      <div class="image-container" @dblclick="like(index)">
        <!--<div class="like-animation"-->
        <!--:class="{ 'show': likeAnimation }">&hearts;-->
        <!--</div>-->
        <img :src="post.postImage"/>
      </div>
      <div class="content">
        <p class="likes" @click="handleFav(post.id)" >
          <span class="heart" :class="{ liked: post.upVoted }">&#9825;</span>
          {{post.likes }} likes</p>
        <p class="caption">
          <span class="username-content">{{ post.username }}</span> {{ post.caption }}
        </p>
        <span class="pub-date">Posted {{ post.publicationDate | pubDate }}</span>
      </div>
    </div>
  </div>
</template>

<script>
  /* global CacheManager */

  import moment from 'moment'
// import {CacheManager} from 'reflectos-sdk'

export default {
  name: 'Instagram',
  data: () => ({
    posts: [{
      id: 1,
      username: 'ureflectio',
      userImage: 'https://scontent-cdg2-1.cdninstagram.com/vp/a8ecb5271e4c31b0fba92d5089e68a33/5CAE87BE/t51.2885-19/s150x150/21820415_118539128819939_6097619693525270528_n.jpg',
      postImage: 'https://scontent-cdg2-1.cdninstagram.com/vp/d222371fc2eb74542b990bf52184f320/5CA3C263/t51.2885-15/e35/45329239_1135537463260506_1359879124098326906_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com',
      likes: 145,
      upVoted: false,
      caption: 'Move the weather watching the last video on YouTube 👌🏻',
      filter: 'perpetua',
      location: 'Bordeaux, France',
      publicationDate: '2018-10-19 22:54'
    }, {
      id: 2,
      username: 'ureflectio',
      userImage: 'https://scontent-cdg2-1.cdninstagram.com/vp/a8ecb5271e4c31b0fba92d5089e68a33/5CAE87BE/t51.2885-19/s150x150/21820415_118539128819939_6097619693525270528_n.jpg',
      postImage: 'https://scontent-cdg2-1.cdninstagram.com/vp/76c760a0b9dd32e9de192b506b50e4db/5C9AFE4A/t51.2885-15/e35/43107545_200398720853553_181539158082282831_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com',
      likes: 129,
      upVoted: false,
      caption: 'Startup week for French High School students 🇫🇷',
      filter: 'perpetua',
      location: 'Bordeaux, France',
      publicationDate: '2018-08-19 12:54'
    }, {
      id: 3,
      username: 'ureflectio',
      userImage: 'https://scontent-cdg2-1.cdninstagram.com/vp/a8ecb5271e4c31b0fba92d5089e68a33/5CAE87BE/t51.2885-19/s150x150/21820415_118539128819939_6097619693525270528_n.jpg',
      postImage: 'https://scontent-cdg2-1.cdninstagram.com/vp/274fdf806dfc7308ac0c1a5d65935207/5C96455D/t51.2885-15/e35/43103343_326645454835783_5638651113427008383_n.jpg',
      likes: 113,
      upVoted: false,
      caption: 'If we could switch the weather as quickly as on uReflect ☀️',
      filter: 'perpetua',
      location: 'Bordeaux, France',
      publicationDate: '2018-06-25 8:54'
    }, {
      id: 4,
      username: 'ureflectio',
      userImage: 'https://scontent-cdg2-1.cdninstagram.com/vp/a8ecb5271e4c31b0fba92d5089e68a33/5CAE87BE/t51.2885-19/s150x150/21820415_118539128819939_6097619693525270528_n.jpg',
      postImage: 'https://scontent-cdg2-1.cdninstagram.com/vp/571a2f7878ec05079e7d32cd66d61b4b/5C912CBD/t51.2885-15/e35/44757603_711790909197090_5097555412801900667_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com',
      likes: 89,
      upVoted: false,
      caption: 'Live demo @ Warm-up days contest 🌪',
      filter: 'perpetua',
      location: 'Bordeaux, France',
      publicationDate: '2018-06-17 22:54'
    }]
    ,
    likeAnimation: false,
    cacheM: new CacheManager('ur-instagram')
  }),
  filters: {
    pubDate: function (val) {
      return moment(val).fromNow()
    }
  },
  mounted () {
    this.cacheM.store('ur-instagram', 'value')
    for (let item of this.posts) {
      item.upVoted = this.cacheM.get('favorite_' + item.id) === null || this.cacheM.get('favorite_' + item.id) === 'false' ? false : true;
    }
    this.mockPosts()
  },
  methods: {
    handleFav(id) {
      if (this.cacheM.get('favorite_' + id) !== null && this.cacheM.get('favorite_' + id) === 'true') {
        this.cacheM.store('favorite_' + id, 'false');
        this.posts[id - 1].upVoted = false;
        this.posts[id - 1].likes -= 1;
      }
      else {
        this.cacheM.store('favorite_' + id, 'true');
        this.posts[id - 1].upVoted = true;
        this.posts[id - 1].likes += 1;
      }
    },
    like: function (index) {
      if (!this.posts[index].upVoted) {
        this.posts[index].upVoted = true
        ++this.posts[index].likes
        // this.showLikeAnimation()
      } else {
        this.posts[index].upVoted = false
        --this.posts[index].likes
      }
    },
    showLikeAnimation: function () {
      setTimeout(() => {
        this.likeAnimation = false
      }, 750)
      this.likeAnimation = true
    },
    mockPosts: function () {
           }
  }
}
</script>

